// common/http.interceptor.js

// vmvuethisvuextoken
const install = (Vue, vm) => {
	// 
	Vue.prototype.$u.http.setConfig({
		baseUrl: 'http://127.0.0.1:8081/api',
		dataType: 'json',
		showLoading: true, // loading
		loadingText: '...', // loading
		loadingTime: 800, // ms
		originalData: true, // 
		loadingMask: true, // loading
	});

	// Token
	Vue.prototype.$u.http.interceptor.request = (config) => {
		// token
		// vuextokenuViewvuex
		// https://uviewui.com/components/globalVariable.html
		// config.header.token = vm.token;
		//config.header.Authorization = "Bearer " + vm.access_token
		config.header.Authorization = vm.vuex_token
		// urlurlthis.$u.get(url)url
		// if(config.url == '/user/login') config.header.noToken = true;
		// configreturn
		return config;
		// returnfalse
		// if(config.url == '/user/rest') return false; // 
	}

	// 
	Vue.prototype.$u.http.interceptor.response = (res) => {
		// 
		const {
			statusCode,
			data
		} = res

		if (statusCode < 400) {
			return data;
		} else if (statusCode == 400) {
			// 
			vm.$u.toast(data.message)
			return false;
		} else if (statusCode == 401) {
			// TODO 401 token
			if (data.message == "Unauthorized") {
				vm.$u.toast("")
			} else {
				// api
				vm.$u.utils.isLogin()
			}
			return false;
		} else {

			return false;
		}
	}

}

export default {
	install
}
