// vmthisvmvuexuView
// https://uviewui.com/js/http.html#%E4%BD%95%E8%B0%93%E8%AF%B7%E6%B1%82%E6%8B%A6%E6%88%AA%EF%BC%9F
const install = (Vue, vm) => {
	// api
	vm.$u.api = {}

	// 
	vm.$u.api.authLogin = params => vm.$u.post('/user/login', params); //  
	vm.$u.api.updateUserBaseInfo = params => vm.$u.post('/user/update', params) // 
	vm.$u.api.getUserBaseInfo = params => vm.$u.get('/user/baseInfo', params) // 
	vm.$u.api.followUser = params => vm.$u.post('/user/follow',params) // 
	
	// 
	vm.$u.api.getSalaryInfoList = params => vm.$u.get('/salaryInfo/list', params) // 
	vm.$u.api.getSalaryInfoDetail = params => vm.$u.get('/salaryInfo/' + params.salaryId) // 
	vm.$u.api.createSalaryInfo = params => vm.$u.post('/salaryInfo/create', params) // 
	vm.$u.api.voteSalaryInfo = params => vm.$u.post('/salaryInfo/vote', params) // 

	// 
	vm.$u.api.getRecruitInfoList = params => vm.$u.get('/recruitInfo/list', params) // 
	vm.$u.api.getRecruitInfoDetail = params => vm.$u.get('/recruitInfo/' + params.scheduleId) // 
	vm.$u.api.followRecruitInfo = params => vm.$u.post('/recruitInfo/follow', params) // 

	// 
	vm.$u.api.getDictionaryItem = params => vm.$u.get('/dictionary/subItem/' + params.code) // 

	// 
	vm.$u.api.createFeedback = params => vm.$u.post('/feedback/create', params) // 
	vm.$u.api.getFeedbackList = params => vm.$u.get('/feedback/list', params) // 

	// 
	vm.$u.api.deletePostInfo = params => vm.$u.post('/postInfo/delete', params) // 
	vm.$u.api.getPostInfoDetail = params => vm.$u.get('/postInfo/' + params.postId) // 
	vm.$u.api.editPostInfo = params => vm.$u.post('/postInfo/edit', params) // 
	vm.$u.api.getPostInfoComment = params => vm.$u.get('/postInfo/comment', params) // 
	vm.$u.api.getEditPostInfoDetail = params => vm.$u.get('/postInfo/edit', params) // 
	vm.$u.api.getPostInfoIds = params => vm.$u.get('/postInfo/ids', params) // 20ID
	vm.$u.api.likePostInfo = params => vm.$u.post('/postInfo/like', params) // 
	vm.$u.api.getPostInfoListByIds = params => vm.$u.get('/postInfo/list', params) // Ids
	vm.$u.api.getPostInfoListBySearch = params => vm.$u.get('/postInfo/search', params) // 
	vm.$u.api.getPostInfoListByUser = params => vm.$u.get('/postInfo/user', params) // 
	vm.$u.api.getPostInfoListByStar = params => vm.$u.get('/postInfo/star', params) // 
	vm.$u.api.createPostInfoComment = params => vm.$u.post('/postInfo/comment', params) // 
	vm.$u.api.starPostInfo = params => vm.$u.post('/postInfo/star', params) // 
}

export default {
	install
}
